.lastLine {
    background: #121212;
    padding: 25px 0 23px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.col {
    display: flex;
    align-items: center;
    justify-content: center;
}

@media (max-width: 776px) {
    .lastLine {
        flex-direction: column;
    }
}